import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
// import BtnAddNew from 'components/common/button/BtnAddNew';
import { Card, Row, Col } from 'antd';
import TableScholarship from './TableScholarship';
import SearchBar from './SearchBar';
import './style.scss'


class ListScholarshipApprovalPage extends Component {
  state = {};

  render() {

    return (
      <Card title="Xét duyệt học bổng">
        <SearchBar />
        <Row>
          <Col span={24}>
            <TableScholarship />
          </Col>
        </Row>
      </Card >
    );
  }
}

export default props => (
  <Page {...props}>
    <ListScholarshipApprovalPage />
  </Page>
);



